import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import PassWord from '../views/PassWord.vue'
import AdminiView from '../views/AdminiView.vue'
import CalendarChart from '../views/CalendarChart.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/pass',
    name: 'pass',
    component: PassWord
  },
  {
    path: '/admini',
    name: 'admini',
    component: AdminiView
  },
  {
    path: '/calendarchart',
    name: 'calendarchart',
    component: CalendarChart
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
