<script>
import { useCookies } from 'vue3-cookies'
import { onMounted } from 'vue'
export default {
  setup() {
    const { cookies } = useCookies()

    onMounted(() => {
      cookies.set("practice", "true", { expires: 12/24 })
      let my_cookie_value = cookies.get("practice")
      console.log(my_cookie_value)
    })

    return {
      cookies: cookies
    }
  }
}
