<template>
  <div>
    <canvas id="driver-chart"></canvas>
  </div>
</template>

<script>
import Chart from 'chart.js'
import driverChartData from './LineChart.js'
import { ref, watch, onMounted } from 'vue'

export default {
  name: 'MyChart',
  props: {
    chartCounter: {
      type: Number,
      default: 0,
      required: true
    }
  },
  setup(chartCounter) {
    let mychart = ref(0)
    const renderChart = (data) => {
      let ctx = document.getElementById('driver-chart');
      mychart.value = new Chart(ctx, data);
    }

    watch(chartCounter, () => {
      mychart.value.update();
    },
    {
      deep: true
    })

    onMounted(() => {
      renderChart(driverChartData);
    })

    return {
      mychart: mychart,
      driverChartData: driverChartData,
      renderChart: renderChart
    }
  }
}
</script>

