<template>
  <div class="pass">
    <body>

      <header>
        <img src="TR.PNG" class="logo">
      </header>

      <main>
        <div class="register">
          <p class="text1">パスワード設定</p>
          <p class="text2">必要な情報を入力してください。</p>
        </div>
        <div class="information">
          <form>
            <table div class="table">
              <tr div class="tr2">
                <th div class="title">メールアドレス</th>
                <th><input div id="email" type="email" v-model="email1"></th>
              </tr>
              <tr div class="tr3">
                <th div class="title">メールアドレス(確認用)</th>
                <th>
                  <input div id="EMAIL" type="email" v-model="email2" v-on:blur="emailChack">
                  <p div id="errorcontent1" v-bind:class="{displaynone:displaynoneemail}">*入力された値が一致しません。</p>
                </th>
              </tr>
              <tr div class="tr4">
                <th div class="title">ID</th> <th><input v-model="ID" type="text"></th>
              </tr>
              <tr div class="tr5">
                <th div class="title">パスワード</th>
                <th>
                  <input div id="password" type="password" maxlength="16" v-model="password1" v-on:blur="passwordCorrect">
                  <p div id="errorcontent2" v-bind:class="{displaynone:displaynonepassword1}"></p>
                </th>
              </tr>
              <tr div class="tr6">
                <th div class="title">パスワード(確認用)</th>
                <th>
                  <input div id="PASSWORD" type="password" maxlength="16" v-model="password2" v-on:blur="passwordChack">
                  <p div id="errorcontent3" v-bind:class="{displaynone:displaynonepassword2}">*入力された値が一致しません。</p>
                </th>
              </tr>
            </table>
            <button type="submit" id="BUTTON" v-bind:class="{button:buttonkey}" v-on:click="buttonClick">送信する</button>
          </form>
        </div>
      </main>

      <footer>
        <span div class="foot">&copy; 2022 TOKAIRIKA,CO,LTD</span>
      </footer>

    </body>
  </div>
</template>

<script>
import { ref } from 'vue'
import { useRouter } from 'vue-router'

export default {
  name: 'PassWord',
  setup() {
    let email1 = ref("")
    let email2 = ref("")
    let displaynoneemail = ref(true)
    const emailChack = () => {
      if(email2.value === "" || email2.value === email1.value){
        displaynoneemail.value = true;
      }
      else {
        displaynoneemail.value = false;
      }
    }
    let password1 = ref("")
    let password2 = ref("")
    let displaynonepassword1 = ref(true)
    let displaynonepassword2 = ref(true)
    const passwordCorrect = () => {
      let chackBox = [];
      let array = password1.value.split('');
      for(let i=0; i<array.length; i++){
        if(array[i].match(/[A-Z]/)){
          chackBox.push('N');
        }
        else if(array[i].match(/[a-z]/)){
          chackBox.push('I');
        }
        else if(array[i].match(/[0-9]/)){
          chackBox.push('C');
        }
        else{
          chackBox.push('E');
        }
      }
            console.log(chackBox);
      let N = chackBox.includes('N');
      let I = chackBox.includes('I');
      let C = chackBox.includes('C');
      let E = chackBox.includes('E');
      let errorContent2 = document.getElementById('errorcontent2');
      if(array.length<8){
        errorContent2.innerText = '*8文字以上にしてください。';
        displaynonepassword1.value = false;
      }
      else if(N === false){
        errorContent2.innerText = '*半角英大文字を入力してください。';
        displaynonepassword1.value = false;
      }
      else if(I === false){
        errorContent2.innerText = '*半角英小文字を入力してください。';
        displaynonepassword1.value = false;
      }
      else if(C === false){
        errorContent2.innerText = '*半角数値を入力してください。';
        displaynonepassword1.value = false;
      }
      else if(E === true){
        errorContent2.innerText = '*指定文字以外は使用出来ません。';
        displaynonepassword1.value = false;
      }
      if(password1.value === ''){
        displaynonepassword1.value = true;
      }
    }
    const passwordChack = () => {
      if(password2.value === "" || password2.value === password1.value){
        displaynonepassword2.value = true;
      }
      else {
        displaynonepassword2.value = false;
      }
    }

    const router = useRouter()
    let buttonkey = ref(true)
    let ID = ref("")
    const buttonClick = () => {
      buttonkey.value = false;
      if(displaynoneemail.value==false && email2.value=='masataka.ito@exc.tokai-rika.co.jp' && ID.value==1234 && displaynonepassword1.value==false && displaynonepassword2.value==false){
        router.push('/');
      }
      else if(displaynoneemail.value===true){
        null;
      }
      else if(!email2.value==='masataka.ito@exc.tokai-rika.co.jp' || !ID.value===1234){
        console.log("mail or ID ちゃう");
      }
      else if(displaynonepassword1.value===true){
        null;
      }
      else if(displaynonepassword2.value===true){
        null;
      }
      else{
        console.log("必要な情報を入力してください");
      }
    }

    return {
      email1: email1,
      email2: email2,
      displaynoneemail: displaynoneemail,
      emailChack: emailChack,

      password1: password1,
      password2: password2,
      displaynonepassword1: displaynonepassword1,
      displaynonepassword2: displaynonepassword2,
      passwordCorrect: passwordCorrect,
      passwordChack: passwordChack,

      buttonClick:  buttonClick,
      buttonkey: buttonkey,
      ID: ID
    }
  }
}

</script>

<style scoped>
