}
.information #errorcontent2 {
  font-size: 8px;
  color:rgba(255, 10, 10, 1.0) ;
  background-color:rgba(255, 200, 200, 1.0) ;
  border: 1px solid;
  border-color: rgba(255, 10, 10, 1.0);
  border-radius:4px ;
